@charset "utf-8";

.brandName {
    font-family: 'Josefin Sans', sans-serif;
}

.iconBtn {
    outline: none !important;
}

.iconBtn:hover, .iconBtn:focus {
    cursor: default;
}
@charset "utf-8";

.card-note {
    transition: 0.8s;
}

button.deleteBtn {
    float: right;
    margin-right: 15px; 
    padding: 10px;
    border-radius: 50px;
    border: none;
    background: #FFCC00;
    box-shadow: 0px 2px 10px grey;
    transition: 0.8s !important;
}

button.deleteBtn:hover .btnIcon {
    color: white;
}
@charset "utf-8";

form {
    width: 30%;
    box-shadow: 0px 2px 10px grey;
    border-radius: 5px;
}

input { 
    border: none !important; 
    font-weight: bold !important;
}

input::-webkit-input-placeholder { font-weight: bold; }

input:-ms-input-placeholder { font-weight: bold; }

input::-ms-input-placeholder { font-weight: bold; }

input::placeholder { font-weight: bold; }

input:focus, textarea:focus {
    box-shadow: none !important;
}

textarea { border: none !important; }

@media screen and (max-width:992px) {
    form { width: 70%; }
}

@media screen and (max-width:576px) {
    form { width: 90%; }
}

button[type="submit"] {
    display: block;
    margin: 10px 30px -30px auto;
    padding: 12px 15px 8px 15px;
    border-radius: 50px;
    border: none;
    background: #FFCC00;
    box-shadow: 0px 2px 10px grey;
    transition: 0.4s;
}

button:active, button:focus {
    outline: none;
}

button:hover {
    background: #343A40;
}

button i {
    color: white;
    font-size: 1.4em !important;
}

/* Textarea Scrollbar */

/* width */
textarea::-webkit-scrollbar {
    width: 5px;
}

/* Track */
textarea::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
textarea::-webkit-scrollbar-thumb {
    background: #FFCC00;
}

/* Handle on hover */
textarea::-webkit-scrollbar-thumb:hover {
    background: #555;
}
@charset "utf-8";

